export default {
	name: 'tab',
	props: {
		tabList: {
			type: Array,
			default() {
				return []
			}
		},
		tabIndex: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			currentIndex: 0
		}
	},
	methods: {
		clickTab(item, index) {
			this.currentIndex = index
			item.index = index
			this.$emit('tab', item)
		}
	},
	watch: {
		tabIndex(newValue) {
			console.log(newValue)
			this.currentIndex = newValue
		}
	}
}
